@import url(//fonts.googleapis.com/css?family=Open+Sans);
body {
  font-family: 'Open Sans';
  color: #343A40;
}


.white-div {
  background-color: #fff;
  padding: 3rem;
  margin: 3rem 7rem;
}


.align-to-end {
  text-align: end;
}

.align-to-center {
  text-align: center;
}

.custom-button-richiesta {
  font-size: large;
  border: 1px solid #45A2F8;
  color: white;
  border-radius: 5px;
  background: #45A2F8;
  margin-top: 2rem;
}

.custom-button-richiesta:hover {
  color: #45A2F8;
  background-color: #efefef;
}


.back-col {
  background-color: #E5E5E5;
}

.title{
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 32.8px;
  margin-left: 15rem;
  margin-right: 15rem;
}

.subtitle{
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
}

.subtitle-proposta{
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
}

.green-check{
  color: #139518;
  width: 88px;
  height: 88px;


}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

