@import url(//fonts.googleapis.com/css?family=Open+Sans);

body {
  font-family: 'Open Sans';
  color: #343A40;
}


.white-div {
  background-color: #fff;
  padding: 3rem;
  margin: 3rem 7rem;
}


.align-to-end {
  text-align: end;
}

.align-to-center {
  text-align: center;
}

.custom-button-richiesta {
  font-size: large;
  border: 1px solid #45A2F8;
  color: white;
  border-radius: 5px;
  background: #45A2F8;
  margin-top: 2rem;
}

.custom-button-richiesta:hover {
  color: #45A2F8;
  background-color: #efefef;
}


.back-col {
  background-color: #E5E5E5;
}

.title{
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 32.8px;
  margin-left: 15rem;
  margin-right: 15rem;
}

.subtitle{
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
}

.subtitle-proposta{
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
}

.green-check{
  color: #139518;
  width: 88px;
  height: 88px;


}
